.range-slider-container {
  width: 300px; /* Пример ширины */
  padding: 10px 0;
  margin: 5px;
}

/* Лейбл над слайдером */
.range-slider-label {
   display: block;
   margin-bottom: 1rem;
   font-size: 0.9rem;
   color: #333;
   font-weight: 500;
}

/* Контейнер для самого слайдера (инпутов и дорожек) */
.range-slider {
  position: relative;
  height: 20px; /* Высота для размещения ползунков */
  display: flex;
  align-items: center;
}

/* Базовые стили для скрытых input type="range" (ползунков) */
.range-slider__thumb,
.range-slider__thumb::-webkit-slider-thumb,
.range-slider__thumb::-moz-range-thumb {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  position: absolute;
  height: 16px; /* Высота ползунка */
  width: 16px;  /* Ширина ползунка */
  background-color: #007bff; /* Цвет ползунка */
  border-radius: 50%;
  border: none;
  cursor: pointer;
  margin: 0;
  padding: 0;
  z-index: 2; /* По умолчанию */
  pointer-events: none; /* Отключаем события на самом input */
}

/* Стили для самих input (дорожка скрыта) */
.range-slider__thumb {
  pointer-events: auto; /* Включаем события для перемещения */
  width: 100%; /* Растягиваем на всю ширину */
  height: 4px; /* Делаем невидимым по высоте, но кликабельным */
  background: none; /* Убираем фон */
  outline: none;
}

/* Позиционирование ползунков */
.range-slider__thumb--zindex-3 {
  z-index: 3;
}
.range-slider__thumb--zindex-4 {
  z-index: 4;
}

/* Webkit стили для ползунка */
.range-slider__thumb::-webkit-slider-thumb {
  pointer-events: auto;
  position: relative; /* Важно для правильного позиционирования */
  margin-top: -6px; /* Коррекция позиции для webkit ( (16px - 4px) / 2 ) */
}

/* Firefox стили для ползунка */
.range-slider__thumb::-moz-range-thumb {
  pointer-events: auto;
  position: relative; /* Важно для правильного позиционирования */
  /* Firefox не нуждается в коррекции margin-top */
}

/* Контейнер для визуальных дорожек */
.range-slider__track-container {
  position: absolute;
  top: 50%;
  left: 8px; /* Половина ширины ползунка */
  right: 8px; /* Половина ширины ползунка */
  height: 4px; /* Высота дорожки */
  transform: translateY(-50%);
  pointer-events: none; /* Не мешает кликам на ползунки */
  z-index: 1;
}

/* Основная серая дорожка */
.range-slider__track {
  position: absolute;
  height: 100%;
  width: 100%;
  background-color: #dee2e6; /* Цвет фона дорожки */
  border-radius: 3px;
}

/* Выделенный диапазон (синий) */
.range-slider__range {
  position: absolute;
  height: 100%;
  background-color: #007bff; /* Цвет выделенного диапазона */
  border-radius: 3px;
  z-index: 2; /* Над серой дорожкой */
}

/* Отображение текущих значений */
.range-slider-values {
  display: flex;
  justify-content: space-between;
  margin-top: 8px;
  font-size: 0.85rem;
  color: #6c757d;
}

/* Стили для отключенного состояния */
.range-slider--disabled {
    opacity: 0.6;
}
.range-slider--disabled .range-slider__thumb,
.range-slider--disabled .range-slider__thumb::-webkit-slider-thumb,
.range-slider--disabled .range-slider__thumb::-moz-range-thumb {
    cursor: not-allowed;
    background-color: #adb5bd; /* Серый ползунок */
}
.range-slider--disabled .range-slider__range {
    background-color: #adb5bd; /* Серый диапазон */
} `, "",{"version":3,"sources":["webpack://./src/components/RangeSlider/RangeSlider.css"],"names":[],"mappings":"AAAA,oBAAoB;AACpB;EACE,YAAY,EAAE,kBAAkB;EAChC,eAAe;EACf,WAAW;AACb;;AAEA,wBAAwB;AACxB;GACG,cAAc;GACd,mBAAmB;GACnB,iBAAiB;GACjB,WAAW;GACX,gBAAgB;AACnB;;AAEA,sDAAsD;AACtD;EACE,kBAAkB;EAClB,YAAY,EAAE,oCAAoC;EAClD,aAAa;EACb,mBAAmB;AACrB;;AAEA,6DAA6D;AAC7D;;;EAGE,wBAAwB;EACxB,qBAAqB;EACrB,gBAAgB;EAChB,kBAAkB;EAClB,YAAY,EAAE,oBAAoB;EAClC,WAAW,GAAG,oBAAoB;EAClC,yBAAyB,EAAE,kBAAkB;EAC7C,kBAAkB;EAClB,YAAY;EACZ,eAAe;EACf,SAAS;EACT,UAAU;EACV,UAAU,EAAE,iBAAiB;EAC7B,oBAAoB,EAAE,qCAAqC;AAC7D;;AAEA,2CAA2C;AAC3C;EACE,oBAAoB,EAAE,qCAAqC;EAC3D,WAAW,EAAE,8BAA8B;EAC3C,WAAW,EAAE,gDAAgD;EAC7D,gBAAgB,EAAE,gBAAgB;EAClC,aAAa;AACf;;AAEA,+BAA+B;AAC/B;EACE,UAAU;AACZ;AACA;EACE,UAAU;AACZ;;AAEA,8BAA8B;AAC9B;EACE,oBAAoB;EACpB,kBAAkB,EAAE,2CAA2C;EAC/D,gBAAgB,EAAE,sDAAsD;AAC1E;;AAEA,+BAA+B;AAC/B;EACE,oBAAoB;EACpB,kBAAkB,EAAE,2CAA2C;EAC/D,gDAAgD;AAClD;;AAEA,qCAAqC;AACrC;EACE,kBAAkB;EAClB,QAAQ;EACR,SAAS,EAAE,6BAA6B;EACxC,UAAU,EAAE,6BAA6B;EACzC,WAAW,EAAE,mBAAmB;EAChC,2BAA2B;EAC3B,oBAAoB,EAAE,iCAAiC;EACvD,UAAU;AACZ;;AAEA,2BAA2B;AAC3B;EACE,kBAAkB;EAClB,YAAY;EACZ,WAAW;EACX,yBAAyB,EAAE,sBAAsB;EACjD,kBAAkB;AACpB;;AAEA,gCAAgC;AAChC;EACE,kBAAkB;EAClB,YAAY;EACZ,yBAAyB,EAAE,+BAA+B;EAC1D,kBAAkB;EAClB,UAAU,EAAE,uBAAuB;AACrC;;AAEA,iCAAiC;AACjC;EACE,aAAa;EACb,8BAA8B;EAC9B,eAAe;EACf,kBAAkB;EAClB,cAAc;AAChB;;AAEA,qCAAqC;AACrC;IACI,YAAY;AAChB;AACA;;;IAGI,mBAAmB;IACnB,yBAAyB,EAAE,mBAAmB;AAClD;AACA;IACI,yBAAyB,EAAE,mBAAmB;AAClD","sourcesContent":["/* Общий контейнер */\n.range-slider-container {\n  width: 300px; /* Пример ширины */\n  padding: 10px 0;\n  margin: 5px;\n}\n\n/* Лейбл над слайдером */\n.range-slider-label {\n   display: block;\n   margin-bottom: 1rem;\n   font-size: 0.9rem;\n   color: #333;\n   font-weight: 500;\n}\n\n/* Контейнер для самого слайдера (инпутов и дорожек) */\n.range-slider {\n  position: relative;\n  height: 20px; /* Высота для размещения ползунков */\n  display: flex;\n  align-ite