import React, { useState, useCallback, useEffect, useRef } from 'react';
import './RangeSlider.css'; // Импортируем стили

interface RangeSliderProps {
  min: number;
  max: number;
  step?: number;
  value: [number, number]; // Массив [minValue, maxValue]
  onChange: (newValue: [number, number]) => void;
  disabled?: boolean;
  label?: string; // Лейбл над слайдером
  showValues?: boolean; // Показывать ли текущие значения
  className?: string; // Пользовательский класс
}

const RangeSlider: React.FC<RangeSliderProps> = ({
  min,
  max,
  step = 1,
  value,
  onChange,
  disabled = false,
  label,
  showValues = false,
  className,
}) => {
  const [minValue, setMinValue] = useState(value[0]);
  const [maxValue, setMaxValue] = useState(value[1]);
  const minValRef = useRef<HTMLInputElement>(null);
  const maxValRef = useRef<HTMLInputElement>(null);
  const rangeRef = useRef<HTMLDivElement>(null);

  // Преобразуем значение в проценты для позиционирования
  const getPercent = useCallback(
    (val: number) => Math.round(((val - min) / (max - min)) * 100),
    [min, max]
  );

  // Обновляем внутреннее состояние при изменении value из пропсов
  useEffect(() => {
    setMinValue(value[0]);
    setMaxValue(value[1]);
  }, [value]);

  // Обновляем позицию выделенного диапазона при изменении значений
  useEffect(() => {
    if (maxValRef.current) {
      const minPercent = getPercent(minValue);
      const maxPercent = getPercent(+maxValRef.current.value); // Берем текущее значение второго ползунка

      if (rangeRef.current) {
        rangeRef.current.style.left = `${minPercent}%`;
        rangeRef.current.style.width = `${maxPercent - minPercent}%`;
      }
    }
  }, [minValue, getPercent]);

  useEffect(() => {
    if (minValRef.current) {
      const minPercent = getPercent(+minValRef.current.value); // Берем текущее значение первого ползунка
      const maxPercent = getPercent(maxValue);

      if (rangeRef.current) {
        rangeRef.current.style.width = `${maxPercent - minPercent}%`;
      }
    }
  }, [maxValue, getPercent]);

  // Обработчик изменения для onChange (вызывается при отпускании ползунка)
  const triggerOnChange = () => {
    onChange([minValue, maxValue]);
  };

  const handleMinChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const newMinValue = Math.min(+event.target.value, maxValue - step); // Гарантируем, что min < max
    setMinValue(newMinValue);
    // Не вызываем onChange здесь, а только при отпускании мыши (onMouseUp/onTouchEnd)
    // или можно добавить проп для real-time обновления
  };

   const handleMaxChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const newMaxValue = Math.max(+event.target.value, minValue + step); // Гарантируем, что max > min
    setMaxValue(newMaxValue);
    // Не вызываем onChange здесь
  };


  const containerClassName = [
      'range-slider-container',
      disabled ? 'range-slider--disabled' : '',
      className,
  ].filter(Boolean).join(' ');

  return (
    <div className={containerClassName}>
       {label && <label className="range-slider-label">{label}</label>}
      <div className="range-slider">
        <input
          type="range"
          ref={minValRef}
          min={min}
          max={max}
          step={step}
          value={minValue}
          onChange={handleMinChange}
          onMouseUp={triggerOnChange} // Вызов onChange при отпускании
          onTouchEnd={triggerOnChange} // Вызов onChange при отпускании на таче
          className="range-slider__thumb range-slider__thumb--zindex-3" // z-index 3
          disabled={disabled}
        />
        <input
          type="range"
          ref={maxValRef}
          min={min}
          max={max}
          step={step}
          value={maxValue}
          onChange={handleMaxChange}
          onMouseUp={triggerOnChange}
          onTouchEnd={triggerOnChange}
          className="range-slider__thumb range-slider__thumb--zindex-4" // z-index 4 (чтобы был поверх min ползунка)
          disabled={disabled}
        />

        <div className="range-slider__track-container">
          <div className="range-slider__track" />
          <div ref={rangeRef} className="range-slider__range" />
        </div>
      </div>
       {showValues && (
         <div className="range-slider-values">
           <span>{minValue}</span>
           <span>{maxValue}</span>
         </div>
       )}
    </div>
  );
};

export default RangeSlider; 