import React, { useState } from 'react';
import type { Meta, StoryObj } from '@storybook/react';
import RangeSlider from './RangeSlider';

const meta: Meta<typeof RangeSlider> = {
  title: 'Components/RangeSlider',
  component: RangeSlider,
  parameters: {
    layout: 'centered',
  },
  tags: ['autodocs'],
  argTypes: {
    min: { control: 'number', description: 'Минимальное значение' },
    max: { control: 'number', description: 'Максимальное значение' },
    step: { control: 'number', description: 'Шаг изменения значения' },
    value: { control: 'object', description: 'Массив [min, max] текущего диапазона' },
    onChange: { action: 'changed', description: 'Callback при изменении диапазона' },
    disabled: { control: 'boolean', description: 'Отключает слайдер' },
    label: { control: 'text', description: 'Лейбл над слайдером' },
    showValues: { control: 'boolean', description: 'Показывать ли текущие значения' },
  },
  args: { // Значения по умолчанию
    min: 0,
    max: 100,
    step: 1,
    value: [25, 75], // Начальный диапазон
    disabled: false,
    label: 'Выберите диапазон',
    showValues: true,
  }
};

export default meta;

// --- Истории ---

export const Default: StoryObj<typeof RangeSlider> = {
  render: (args) => {
    const [currentValue, setCurrentValue] = useState<[number, number]>(args.value);
    return (
      <RangeSlider
        {...args}
        value={currentValue}
        onChange={(newValue) => setCurrentValue(newValue)}
      />
    );
  },
};

export const PriceRange: StoryObj<typeof RangeSlider> = {
  args: {
    min: 1000,
    max: 50000,
    step: 100,
    value: [5000, 30000],
    label: 'Диапазон цен',
    showValues: true,
  },
  render: (args) => {
    const [currentValue, setCurrentValue] = useState<[number, number]>(args.value);
    return (
      <RangeSlider
        {...args}
        value={currentValue}
        onChange={(newValue) => setCurrentValue(newValue)}
      />
    );
  },
};


export const WithoutValues: StoryObj<typeof RangeSlider> = {
  args: {
    ...Default.args, // Берем аргументы из Default
    label: 'Слайдер без значений',
    showValues: false, // Скрываем значения
  },
  render: (args) => {
    const [currentValue, setCurrentValue] = useState<[number, number]>(args.value);
    return (
      <RangeSlider
        {...args}
        value={currentValue}
        onChange={(newValue) => setCurrentValue(newValue)}
      />
    );
  },
};


export const Disabled: StoryObj<typeof RangeSlider> = {
    args: {
        ...Default.args,
        label: 'Отключенный слайдер',
        value: [10, 90],
        disabled: true,
    },
     render: (args) => {
        return <RangeSlider {...args} />;
    },
}; 